*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.heading{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  color: tomato;
  font-size: 40px;
  font-weight: 700;
  margin: 2% 0;
}

.stock-selection{
  margin-top: 5%;
}

.answer-stock{
  margin-top: 10%;
  padding: 5% 10% 5% 10%;
  background-color: rgb(156, 235, 235);
  border: 2px solid rgb(104, 104, 241);
  border-radius: 10%;
}
.answer-stock h2{
  font-size: 30px;
  font-weight: 700;
}
.answer-stock h2 span{
  color: red;
}
.answer-stock  p{
  font-size: 40px;
  color: green;
}
.dropdown{
  width: 100%;
  height: 30%;
  font-size: 30px;
}
.dropdown option {
  font-size: 20px;
  margin: 1% 0;
}